import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { COUNTRY } from '../../assets/file/countries';
import { CURRENCY } from '../../assets/file/currencies-with-flags';

/**
 * Generated class for the ApplyCashlineTenorPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-apply-cashline-tenor',
  templateUrl: 'apply-cashline-tenor.html',
})
export class ApplyCashlineTenorPage {
  isShowModal2n: boolean = false;
  list_country = COUNTRY.country_list;
  list_currency = CURRENCY.currency_list;
  selected_item;
  list_tenor = [
    {
      month: "36 Bulan 2,09%",
      tenor: "Rp. 730.167/Bulan",
      model: false
    },
    {
      month: "24 Bulan 2,09%",
      tenor: "Rp. 359.167/Bulan",
      model: true
    },
    {
      month: "12 Bulan 2,09%",
      tenor: "Rp. 359.167/Bulan",
      model: false
    }
  ]
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewWillEnter() {
    console.log('ionViewDidLoad ApplyCashlineTenorPage');
    console.log(this.list_country);
    this.list_tenor.forEach((el, idx) => {
      el['id'] = idx;
    })
  }

  handlerModal() {
    this.isShowModal2n = !this.isShowModal2n;
  }

  selectItem(item) {
    console.log(item);
    this.list_tenor.map((el: any, idx) => {
      if (el.id == item.id) {
        el.model = true;
        this.selected_item = el;
      } else {
        el.model = false;
      }
    })
  }

}



